// mockjs
const Mock = require('mockjs')

//  mock.Random 
const Random = Mock.Random

// 
const verifyList = function (obj) {
  let data = []
  let bindState = ['', '']
  obj.body.type === '1' ? bindState.push('') : bindState.push('')
  let num = Mock.mock({
    'num|1-14': 1
  })
  for (let i = 0; i < num.num; i++) {
    let data2 = Mock.mock({
      'state|1': bindState,
      'phone': /^1[3-9][0-9]{9}$/
    })
    let newObject = {
      // title: Random.csentence(5, 30), //  Random.csentence( min, max )
      // image: Random.dataImage('300x250', 'mock'), // Random.dataImage( size, text )  Base64 
      // user: Random.cname(), // Random.cname() 
      date: Random.date() + ' ' + Random.time() // Random.date(),yyyy-MM-ddRandom.time() 
    }
    Object.assign(newObject, data2)
    data.push(newObject)
  }

  return data
}

// 
const boundList = function (obj) {
  let data = []
  let num = Mock.mock({
    'num|1-14': 1
  })
  for (let i = 0; i < num.num; i++) {
    let data2 = Mock.mock({
      'phone': /^1[3-9][0-9]{9}$/
    })
    let newObject = {
      date: Random.date() + ' ' + Random.time() // Random.date(),yyyy-MM-ddRandom.time() 
    }
    Object.assign(newObject, data2)
    data.push(newObject)
  }

  return data
}

/**
 * Mock.mock( url, post/get , )
 */
// 
Mock.mock('/isLogin', 'post', {
  'code|0-2': 0
})
// 
Mock.mock('/isBind', 'post', {
  'code|0-1': 0
})
// 
Mock.mock('/bind-verify-list', 'post', verifyList)
// 
Mock.mock('/bound-user-list', 'post', boundList)



