// mockjs
const Mock = require('mockjs')

//  mock.Random 
const Random = Mock.Random

const data = function () {
  let cash = {
    cash: Random.float(0, 10000, 2, 2)
  }
  return cash
}

// Mock.mock( url, post/get , )
Mock.mock('/keter/withdraw-cash', 'post', data)



